import React, { useRef } from "react";

import { Row, Col, Card, CardBody, CardHeader, CardTitle } from "reactstrap";

import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { a11yDark } from "react-syntax-highlighter/dist/esm/styles/prism";

import PanelHeader from "../../components/PanelHeader/PanelHeader.js";
import ButtonLink from "../../components/Buttons/ButtonLink";

import { CodeExamplesProjectStructure } from "../../variables/codeExamplesProjectStructure";
import { useScrollToAnchorByParams, useChangeAnchorLink } from "utils";

//= ==============================================================================================
function ProjectStructure() {
  const refPatternImportance = useRef(null);
  const refOldPattern = useRef(null);
  const refOurPattern = useRef(null);

  useScrollToAnchorByParams({
    refPatternImportance,
    refOldPattern,
    refOurPattern,
  });

  const changeAnchorLink = useChangeAnchorLink();

  //= ==============================================================================================
  return (
    <>
      <PanelHeader size="sm" />
      <div className="content">
        <Row>
          <Col md="9">
            <Card>
              <CardHeader>
                <h5 className="title">
                  Documentao do padro de projetos dos aplicativos
                </h5>

                <p className="text-muted">
                  A estruturao dos projetos  definida por um padro de
                  projetos que separa view e logic
                </p>
              </CardHeader>

              <CardBody>
                <p>
                  <h6>
                    <a id="projectPatternImportance">
                      A respeito do padro de projetos
                    </a>
                  </h6>

                  <h7
                    ref={refPatternImportance}
                    id="createProjectsWithoutTemplate"
                    class="font-weight-bold"
                  >
                    Importncia de padres de projeto
                  </h7>

                  <p>
                    Padres de projeto so essenciais principalmente quando
                    estamos trabalhando com um time, isso nos permite conseguir
                    falar a mesma linguagem, entender o cdigo um do outro sem a
                    necessidade de um grande esforo, nos proporciona uma forma
                    organizada de gerenciar nossos componentes, telas,
                    configuraes, etc. Isso mostra o valor de desenvolver
                    seguindo um padro objetivo e coerente, alm disso, um
                    padro de projeto pode melhorar a velocidade e facilidade de
                    resolues de demandas de forma exponencial. Com isso temos
                    benefcios tais como:
                  </p>

                  <ul>
                    <li>Aumenta a mantenabilidade do cdigo;</li>
                    <li>Mais fcil de testar;</li>
                    <li>Aumento da produo;</li>
                    <li>
                      Elimina duplicao de cdigo promovendo a reutilizao.
                    </li>
                  </ul>

                  <br />
                  <br />

                  <h7
                    ref={refOldPattern}
                    id="commomPattern"
                    class="font-weight-bold"
                  >
                    Tudo junto e misturado: Um pouco sobre o padro comum
                  </h7>

                  <p>
                     comum na arquitetura de projetos popular do react-native
                    ser mantido a view e a logic no mesmo lugar, bem diferente
                    da arquitetura padro para Android Nativo (Java/Kotlin) por
                    exemplo, que mantm toda a parte visual em xml's e toda a
                    parte lgica de forma separada e ainda de forma segregada.
                    Ento de forma comum teramos algo assim:
                  </p>

                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesProjectStructure.normalMode}
                    />
                  </pre>

                  <p>
                    No exemplo acima observamos os seguintes pontos negativos
                    dessa arquitetura:
                  </p>

                  <ul>
                    <li>
                      A parte lgica e visual ficam no mesmo arquivo, no h
                      nada muito slido nisso, ainda mais quando o cdigo de uma
                      tela evolui e se torna maior.
                    </li>
                    <li>
                      Outro ponto interessante de se observar  sobre o
                      entendimento do JSX dessa tela, conseguimos reconhecer
                      alguns elementos visuais mas ficaria bem mais entendvel
                      se tivesse sido segmentado os elementos visuais da tela
                      utilizando funes ento a partir dos nomes conseguiramos
                      entender os diferentes componentes que montam essa tela de
                      Profile.
                    </li>
                    <li>
                      E ainda h outro coisa, e se eu quisesse reutilizar
                      algumas daquelas funes em outros lugares? A exemplo
                      temos o mtodo de logout que poderia ser utilizado como um
                      hook global, assim sempre que precisassmos realizar
                      logout em qual fosse a tela poderiamos importar esse hook
                      e us-lo aproveitamento a mesma lgica em diversos
                      lugares.
                    </li>
                  </ul>

                  <br />
                  <br />

                  <h7
                    ref={refOurPattern}
                    id="knowingVLPattern"
                    class="font-weight-bold"
                  >
                    Conhecendo o padro de projetos VL (View {"&"} logic)
                  </h7>

                  <p>
                    Nosso padro de projeto tenta resolver os problemas acima,
                    sem criar novos problemas mas ainda mais solues pro dia a
                    dia durante nosso desenvolvimento. Vamos usar o exemplo do
                    cdigo acima refatorado, este cdigo evoluiu agora, ele est
                    maior, tem mais elementos visuais e este padro faz sentido
                    antes e agora que o cdigo est maior, porque nunca sabemos
                    quando ser necessrio expandir nosso cdigo devido novas
                    demandas.
                  </p>

                  <p>
                    Vamos primeiro criar uma pasta para nossa parte lgica, e
                    por padro sempre chamamos de logic, ficar assim:
                  </p>

                  <div>
                    <img
                      height="30%"
                      alt="..."
                      src={require("assets/img/logicViewFolderStructure.png")}
                    />
                  </div>
                  <br />

                  <p>
                    Precisamos tambm criar uma pasta chamada hooks, que conter
                    nossos hooks globais, tudo que poder ser utilizado em
                    diversas telas deve ficar dentro de hooks globais:
                  </p>

                  <div>
                    <img
                      height="30%"
                      alt="..."
                      src={require("assets/img/globalHooksFolder.png")}
                    />
                  </div>
                  <br />

                  <p>
                    Dentro de logic/index.js colocaremos toda nossa parte lgica
                    que tnhamos junto com a parte visual anteriormente,
                    utilizaremos hooks para a extrao da parte lgica, ficar
                    algo assim:
                  </p>

                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesProjectStructure.ourModeLogic}
                    />
                  </pre>

                  <p>
                    Vamos entender sobre algumas partes importantes da logic:
                  </p>

                  <dl>
                    <dt>
                      <h7 class="text-muted">1. useBetween</h7>{" "}
                    </dt>
                    <dd>
                      O{" "}
                      <a
                        target="_blank"
                        href="https://www.npmjs.com/package/use-between"
                      >
                        <h7 class="text-muted">useBetween</h7>
                      </a>{" "}
                       uma lib que nos permite criar uma espcie de{" "}
                      <a
                        target="_blank"
                        href="https://refactoring.guru/pt-br/design-patterns/singleton"
                      >
                        <h7 class="text-muted">singleton</h7>
                      </a>{" "}
                      com os nossos states, dessa forma conseguimos acessar um
                      state de vrios lugares e sempre que o acessarmos e o
                      modificarmos estaremos usando a mesma instncia, isso nos
                      permite ter um poder de compartilhamento e reutilizao
                      imenso, ou seja, o state e seu valor que for modificado na
                      tela de Profile se acessado da tela de login ter o mesmo
                      valor e vice-versa.
                    </dd>
                    <dt>
                      <h7 class="text-muted">2. useLogicStates</h7>{" "}
                    </dt>
                    <dd>
                      useLogicStates  uma funo que sempre conter a
                      declarao de todos os nosso states, refs e etc.
                      Precisamos declar-los e retorn-los, depois disso
                      passamos o useLogicStates como parmetro do useBetween
                      criando uma nicia e compartilhavl instncia dos itens
                      retornados pelo useLogicState.
                    </dd>
                    <dt>
                      <h7 class="text-muted">3. useSharedState</h7>{" "}
                    </dt>
                    <dd>
                      A forma que acessamos os itens declarados dentro de
                      useLogicStates agora  realizando uma desconstruo de
                      useSharedState,  possvel import-lo em qualquer lugar e
                      fazer a desconstruo obtendo qualquer item do
                      useLogicState e utiliz-los sem problemas.
                    </dd>
                  </dl>

                  <hr />

                  <p>
                    Assim que definimos estas partes, conseguimos dar andamento
                    na extrao da lgica de nossas telas utilizando hooks, os
                    hooks devem conter as seguintes regras:
                  </p>

                  <ul>
                    <li>
                      O seu nome deve iniciar com o prefixo{" "}
                      <h7 class="text-muted">use</h7>:
                    </li>

                    <p class="font-weight-bold">Maneira Errnea</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamplesProjectStructure.hookNameWrongWay}
                      />
                    </pre>

                    <p class="font-weight-bold">Maneira Correta</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={CodeExamplesProjectStructure.hookNameRightWay}
                      />
                    </pre>

                    <li>
                      No deve ser usado a palavra{" "}
                      <h7 class="text-muted">set</h7> depois do prefixo assim
                      quando instanciarmos um hook no correremos o risco de
                      confund-lo com um setter do useState.
                    </li>

                    <p class="font-weight-bold">Maneira Errnea</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={
                          CodeExamplesProjectStructure.hookDontUseSetWrongWay
                        }
                      />
                    </pre>

                    <p class="font-weight-bold">Maneira Correta</p>
                    <pre>
                      <SyntaxHighlighter
                        language="js"
                        style={a11yDark}
                        PreTag="div"
                        children={
                          CodeExamplesProjectStructure.hookDontUseSetRightWay
                        }
                      />
                    </pre>
                  </ul>

                  <hr />

                  <p>
                    Precisamos discutir alguns pontos importantes em relao a
                    logic que podem gerar dvida em uma refatorao de um padro
                    de projeto comum para o padro de projeto VL.
                  </p>

                  <h7 class="text-muted">
                    Sobre o uso de async dentro de hooks:
                  </h7>

                  <p>
                    No podemos colocar async no escopo da array function
                    retornada nos hooks:
                  </p>

                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesProjectStructure.hookAsyncWronWay}
                    />
                  </pre>

                  <p>
                    Ento precisamos utilizar um recurso do js chamado IFFE
                    (Immediately-invoked function expression), assim conseguimos
                    definir uma funo dentro do escopo do hook como async e
                    cham-la ao mesmo tempo:
                  </p>

                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesProjectStructure.hookAsyncRightWay}
                    />
                  </pre>

                  <h7 class="text-muted">
                    Sobre a passagem de parametros para os hooks:
                  </h7>

                  <p>
                    Conseguimos passar parmetros na declarao de um hook,
                    porm existem duas formas, uma s  possvel passar
                    parmetros no momento da instnciao, outro conseguimos
                    passar parmetros no momento em que o hook  chamado, na
                    primeira forma teramos algo assim:
                  </p>

                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={
                        CodeExamplesProjectStructure.hookParamOnInstance
                      }
                    />
                  </pre>

                  <p>
                    Na segunda forma o valor passado como parmetro pode ser
                    dinmico, assim podemos passar o parmetro no momento da
                    chamada e no somente no momento da instnciao:
                  </p>

                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesProjectStructure.hookParamOnCalled}
                    />
                  </pre>

                  <hr />

                  <h7 class="text-muted">
                    Agora vamos conhecer e entender a parte visual e utilizar a
                    logic dentro da View, na parte visual teremos algo assim:
                  </h7>

                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesProjectStructure.ourModeView}
                    />
                  </pre>

                  <iframe
                    src="https://codesandbox.io/embed/new?codemirror=1"
                    style={{
                      width: "100%",
                      height: 500,
                      border: 0,
                      borderRadius: 4,
                      overflow: "hidden",
                    }}
                    allow="accelerometer; ambient-light-sensor; camera; encrypted-media; geolocation; gyroscope; hid; microphone; midi; payment; usb; vr; xr-spatial-tracking"
                    sandbox="allow-forms allow-modals allow-popups allow-presentation allow-same-origin allow-scripts"
                  ></iframe>
                </p>
              </CardBody>
            </Card>
          </Col>

          <Col
            style={{
              position: "fixed",
              top: "11%",
              right: "0px",
              width: "22%",
            }}
          >
            <Card className="card-user">
              <CardHeader>
                <CardTitle>Guia Rpido</CardTitle>
              </CardHeader>

              <CardBody>
                <p>
                  <a className="text-muted" href="#projectPattern">
                    A respeito do padro de projetos:
                  </a>
                  <ul>
                    <li>
                      <ButtonLink
                        link="Importncia de padres de projeto"
                        onClick={() => changeAnchorLink("refPatternImportance")}
                      />
                    </li>

                    <li>
                      <ButtonLink
                        link="Tudo junto e misturado: Um pouco sobre o padro comum"
                        onClick={() => changeAnchorLink("refOldPattern")}
                      />
                    </li>

                    <li>
                      <ButtonLink
                        link="Conhecendo o padro de projetos VL(View e logic)"
                        onClick={() => changeAnchorLink("refOurPattern")}
                      />
                    </li>
                  </ul>
                </p>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
}

export default ProjectStructure;
