import React, { useRef } from "react";
import { Row, Col, Card, CardBody, CardHeader, CardTitle } from "reactstrap";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { a11yDark } from "react-syntax-highlighter/dist/esm/styles/prism";

import PanelHeader from "../../components/PanelHeader/PanelHeader.js";
import ButtonLink from "../../components/Buttons/ButtonLink";

import { CodeExamplesStructure } from "../../variables/codeExamplesStructure";
import { useScrollToAnchorByParams, useChangeAnchorLink } from "utils";

//= ==============================================================================================
function EnvironmentDivision() {
  const refWhyEnvironmentDivision = useRef(null);
  const refDoingNativeEnvironmentDivision = useRef(null);
  const refDoingReactNativeEnvironmentDivision = useRef(null);

  useScrollToAnchorByParams({
    refWhyEnvironmentDivision,
    refDoingNativeEnvironmentDivision,
    refDoingReactNativeEnvironmentDivision,
  });

  const changeAnchorLink = useChangeAnchorLink();

  //= ==============================================================================================
  return (
    <>
      <PanelHeader size="sm" />
      <div className="content">
        <Row>
          <Col md="9">
            <Card>
              <CardHeader>
                <h5 className="title">Diviso de ambientes</h5>
                <p className="text-muted">
                  A estruturao dos projetos  definida por uma stack de
                  qualidade que aborda vrios aspectos
                </p>
              </CardHeader>

              <CardBody>
                <p>
                  <h6 id="newProjectsSettings">
                    A respeito da configurao de novos projetos mobile
                  </h6>
                  <p>
                    Se o projeto foi criado atrves do template essas
                    configuraes j foram realizadas e essa parte pode ser
                    pulada, porm  interessante rever essa sesso para entender
                    o porque de cada ferramenta e como elas funcionam para que
                    seja possvel solucinar determinadas situaes, realizar
                    manutenes e entender como e porque o projeto se commporta
                    como ele se comporta e absorver o mximo dessas
                    configuraes para facilitar o desenvolvimento.
                  </p>

                  <br />

                  <hr />
                  <h7
                    ref={refWhyEnvironmentDivision}
                    id="whyDivideEnvironments"
                    class="font-weight-bold"
                  >
                    O porque e para que ter uma diviso de ambientes
                  </h7>
                  <p>
                    A diviso de ambientes em DEV/QAS/PRD no acontece somente
                    em relao ao servidor mas tambm em relao aos apps a
                    nvel de cdigo nativo e cdigo voltado ao react-native
                    puramente.
                  </p>
                  <ol>
                    <li>
                      <h7 class="text-muted">
                        A diviso de ambientes define vriaveis de ambientes
                        para cada ambiente criando a possibilidade de usar
                        configuraes de forma customizadas para cada ambiente.
                      </h7>
                    </li>
                    <li>
                      {" "}
                      <h7 class="text-muted">
                        Dificulta a possibilidade de subir .aab's para a Play
                        Store por exemplo apontando para o ambiente errado, DEV
                        ou QAS ao invs de PRD.
                      </h7>
                    </li>
                    <li>
                      <h7 class="text-muted">
                        Permite j executar o projeto no ambiente desejado.
                      </h7>
                    </li>
                    <li>
                      <h7 class="text-muted">
                        Permite ter uma aab/apk de cada ambiente no mesmo
                        dispostiivo sem a necessidade de desinstalar nenhuma apk
                        de um ambiente para instalar a outra.
                      </h7>
                    </li>
                  </ol>

                  <br />

                  <hr />

                  <h7
                    ref={refDoingNativeEnvironmentDivision}
                    id="divideEnvironmentsNative"
                    class="font-weight-bold"
                  >
                    Realizando a configurao de diviso de ambientes na parte
                    nativa - Android
                  </h7>
                  <p>
                    Dentro da pasta <h7 class="text-muted">android/src/</h7>{" "}
                    ser encontrado duas pastas <h7 class="text-muted">main</h7>{" "}
                    e <h7 class="text-muted">debug.</h7> Criaremos mais trs
                    pastas sendo elas,{" "}
                    <h7 class="text-muted">dev, qas e prd.</h7> A estutura de
                    pasta deve ficar assim:
                  </p>
                  <div className="image">
                    <img
                      alt="..."
                      src={require("assets/img/EnvironmentSpliting.png")}
                    />
                  </div>
                  <br />
                  <p>
                    O contedo destas pasta pode ser o mesmo da pasta{" "}
                    <h7 class="text-muted">debug</h7>, com a pasta res
                    encontrado dentro da pasta <h7 class="text-muted">main</h7>{" "}
                    porm com alguns detalhes a parte, dentro do AndroidManifest
                    a tag manifest deve conter a propriedade package que deve
                    ser igual ao bundleId do projeto que pode ser encontrado
                    dentro da pasta <h7 class="text-muted">main</h7> na tag
                    manifest na propriedade <h7 class="text-muted">package</h7>.
                    O cdigo deve ficar semelhante a isto:
                  </p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.androidManifestExample}
                    />
                  </pre>
                  <p>
                    Sobre o contudo da pasta <h7 class="text-muted">res</h7>{" "}
                    deve ser os mesmo que existe dentro da pasta{" "}
                    <h7 class="text-muted">main/res</h7>, os detalhes que devem
                    ser observados so os seguintes:
                  </p>
                  <ol>
                    <li>
                      {" "}
                      <h7 class="text-muted">
                        As pastas iniciadas com{" "}
                        <h7 class="text-muted">mimmap</h7> so as pastas
                        responsveis por armazenar os cones do aplicativo,
                        sejam eles de notificao ou do prprio aplicativo
                        quando instalado no celular. Caso necessrio pode se
                        definir cones diferentes para cada ambiente, se no for
                        necessrio essas pastas podem se manter iguais esto na
                        pasta main/res.
                      </h7>
                    </li>
                    <li>
                      <h7 class="text-muted">
                        Em relao a pasta <h7 class="text-muted">values</h7>,
                        ela  responsvel por conter strings e estilos de acesso
                        global. No arquivo styles.xml no  necessrio nenhuma
                        alterao, a no ser que queira definir um determinado
                        estilo para cada ambiente, j em relao ao arquivo
                        strings.xml, devemos colocar o nome do app com o sufixo
                        igual ao nome do ambiente para cada um dos ambientes,
                        devemos tambm configurar a chave do CodePush para cada
                        ambiente, haver uma sesso do CodePush mais adiante.
                      </h7>
                    </li>
                  </ol>
                  <p>
                    O cdigo do arquivo values deve ficar semelhante a isto:
                  </p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.valuesXmlExample}
                    />
                  </pre>
                  <p>
                    A viso final das pastas deve ser assim para os trs
                    ambientes:
                  </p>
                  <div>
                    <img
                      alt="..."
                      src={require("assets/img/finalVisionFolders.png")}
                    />
                  </div>
                  <br />
                  <p>
                    Agora temos que partir para as configuraes do gradle que
                    vai ser responsvel por gerenciar tudo para gente de forma
                    atutomtica de acordo com o que dissermos para ele. Temos
                    dois arquivos build.gradle, essa configurao inicialmente
                    acontecer no que est localizado no caminho{" "}
                    <h7 class="text-muted">android/app</h7> e logo aps
                    partiremos para o outro.
                  </p>
                  <p>
                    Encontre dentro do arquivo uma propriedade chamada{" "}
                    <h7 class="text-muted">project.ext.react</h7>, de forma
                    inicial ela estar parecida com isto:
                  </p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.projectExtReactBefore}
                    />
                  </pre>
                  <p>Ns a mudaremos para que se parea assim:</p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.projectExtReactAfter}
                    />
                  </pre>
                  <p>
                    Agora daremos ateno a propriedade android e suas sub
                    propriedades, dentro da propriedade
                    <h7 class="text-muted">defaultConfig</h7> encontraremos algo
                    assim:
                  </p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.defaultConfigBefore}
                    />
                  </pre>
                  <p>Editaremos para ficar semelhante a isto:</p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.defaultConfigAfter}
                    />
                  </pre>
                  <p>
                    O prximo passo  configurarmos a parte de signing de cada
                    ambiente que define qual chave ser usada para cada, iremos
                    criar uma propriedade para cada ambiente que temos, esses
                    ambientes recebero as mesmas configuraes da propriedade
                    debug. A propriedade que precisamos mudar  a{" "}
                    <h7 class="text-muted">signingConfig</h7>, de forma inicial
                    ela estar desta forma:
                  </p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.signingConfigBefore}
                    />
                  </pre>
                  <p>Editaremos para ficar semelhante a isto:</p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.signingConfigAfter}
                    />
                  </pre>
                  <p>
                    Em seguida precisamos definir os tipos de build que teremos
                    para o nosso app, este passo que nos permitir ter mais de
                    uma aab/apk de ambientes diferentes instalada no nosso
                    dispositivo sem que seja necessrio desinstalar a aab/apk
                    para instalar outra. Definiremos agora as
                    <h7 class="text-muted">buildTypes</h7>, ou tambm conhecidas
                    como variants. De forma inicial teremos algo semelhante a
                    isso:
                  </p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.buildTypesBefore}
                    />
                  </pre>
                  <p>Editaremos para ficar semelhante a isto:</p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.buildTypesAfter}
                    />
                  </pre>
                  <br />

                  <hr />

                  <h7
                    ref={refDoingReactNativeEnvironmentDivision}
                    id="divideEnvironmentsReactNative"
                    class="font-weight-bold"
                  >
                    Realizando a configurao de diviso de ambientes na parte
                    do react-native - Android
                  </h7>
                  <p>
                    Agora partiremos para o cdigo no lado do react-native,
                    comearemos com a criao da pasta{" "}
                    <h7 class="text-muted">config</h7> responsvel pela
                    definio das variveis de cada ambiente, como nome do
                    ambiente, para qual porta deve apontar quando realizada
                    alguma requisio, etc. A estrutura da pasta deve ser algo
                    semelhante a isto:
                  </p>
                  <div>
                    <img
                      height="250"
                      width="300"
                      alt="..."
                      src={require("assets/img/environmentsReactNative.png")}
                    />
                  </div>
                  <br />
                  <p>
                    O contedo do arquivo index na pasta{" "}
                    <h7 class="text-muted">config/</h7> dever ser igual a isto:
                  </p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.configFileContent}
                    />
                  </pre>
                  <p>
                    Em relao ao contedo do arquivo index na pasta{" "}
                    <h7 class="text-muted">config/env/</h7> dever ser assim:
                  </p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.envFileContent}
                    />
                  </pre>
                  <p>
                    J nas pastas <h7 class="text-muted">dev/qas/prd</h7>{" "}
                    teremos um objeto preenchido de forma diferente pra cada um
                    dos ambientes definindo configuraes especficas para cada
                    um, um exemplo seria:
                  </p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.devFileExample}
                    />
                  </pre>
                  <p>
                    Na prxima parte o recomendado  simplesmente copiar a pasta{" "}
                    <h7 class="text-muted">scripts</h7> presentes na raiz de
                    projetos existentes, ou do prprio respositrio do nosso
                    template oficial, no vou abordar todos os scripts, mas 
                    importante enterdermos os principais e como eles funcionam.
                    Os scripts mais importantes so estes:
                  </p>

                  <dl>
                    <dt>
                      <h7 class="text-muted">1. setEnv.js</h7>
                    </dt>
                    <dd>
                      <p>
                        Respnsvel por mudar o contedo do arquivo{" "}
                        <h7 class="text-muted">config/env</h7> dentro do projeto
                        definindo de qual arquivo ele deve obter as
                        configuraes.
                      </p>
                    </dd>
                    <dt>
                      <h7 class="text-muted">2. validParams.js</h7>
                    </dt>
                    <dd>
                      {" "}
                      <p>Valida quais ambientes so aceitos.</p>
                    </dd>
                    <dt>
                      <h7 class="text-muted">3. writeFile.js</h7>
                    </dt>
                    <dd>
                      <p>
                        Mtodo utilitrio que  capaz de sobreescrever o
                        contedo de um arquivo.
                      </p>
                    </dd>
                    <dt>
                      <h7 class="text-muted">4. checkIsRunning.js</h7>
                    </dt>
                    <dd>
                      <p>
                        Verifica se o aplicativo j est em execuo,  bem til
                        para tomar decises, como por exemplo quando executamos
                        o mesmo app para android e iOS ao mesmo tempo e no
                        queremos limpar a build duas vezes, assim ele limparia
                        somente na primeira execuo.
                      </p>
                    </dd>
                    <dt>
                      <h7 class="text-muted">
                        <h7 class="text-muted">5. prepareEnvDevelopment.js</h7>
                      </h7>
                    </dt>
                    <dd>
                      {" "}
                      <p>
                         o script principal que ir executar vrios outros,
                        tomando as decises necessrias para cada situao.
                      </p>
                    </dd>
                  </dl>

                  <p>
                    Agora que j conhecemos os princicipais scripts, podemos
                    partir para o <h7 class="text-muted">package.json</h7> do
                    projeto e criar alguns alias para comandos responsveis para
                    execuo do projeto, dentro de scripts vamos adicionar os
                    seguintes comandos:
                  </p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.packageJsonCommands}
                    />
                  </pre>
                  <p>
                    Temos dois comandos sendo executados, o primeiro comando 
                    responsvel por preparar o projeto de acordo com o ambiente
                    que passamos como primeiro parmetro e de considerar o
                    sistema operacional que  o segundo parmetro. O prximo
                    comando  responsvel por executar a aplicao e tem efeito
                    na parte nativa do aplicativo. Definimos qual o sufixo do
                    app e qual buildType queremos executar.
                  </p>
                  <p>
                    Aps isso deve ser possvel executar a aplicao caso tenha
                    sido feita a configurao de forma correta ou no haja mais
                    nenhuma configurao necessrio no projeto. Se quisermos
                    executar em dev podera-mos executar o seguinte comando:
                  </p>
                  <pre>
                    <SyntaxHighlighter
                      language="js"
                      style={a11yDark}
                      PreTag="div"
                      children={CodeExamplesStructure.runAppExample}
                    />
                  </pre>
                </p>
              </CardBody>
            </Card>
          </Col>

          <Col
            style={{
              position: "fixed",
              top: "11%",
              right: "0px",
              width: "22%",
            }}
          >
            <Card className="card-user">
              <CardHeader>
                <CardTitle>Guia Rpido</CardTitle>
              </CardHeader>

              <CardBody>
                <p>
                  <a className="text-muted" href="#newProjectsSettings">
                    A respeito da configurao de novos projetos mobile:
                  </a>
                  <ul>
                    <li>
                      <ButtonLink
                        link="O porque e para que ter uma diviso de ambientes"
                        onClick={() =>
                          changeAnchorLink("refWhyEnvironmentDivision")
                        }
                      />
                    </li>
                    <li>
                      <ButtonLink
                        link="Realizando a configurao de diviso de ambientes na parte nativa - Android"
                        onClick={() =>
                          changeAnchorLink("refDoingNativeEnvironmentDivision")
                        }
                      />
                    </li>
                    <li>
                      <ButtonLink
                        link="Realizando a configurao de diviso de ambientes na parte do react-native - Android"
                        onClick={() =>
                          changeAnchorLink(
                            "refDoingReactNativeEnvironmentDivision"
                          )
                        }
                      />
                    </li>
                  </ul>
                </p>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
}

export default EnvironmentDivision;
